<template>
  <div>
    <app-header-row :item="service"></app-header-row>
    <app-alert></app-alert>
<!--    <v-btn v-if="data.receipt" @click="$refs.pdfComponent.print()"></v-btn>-->
    <pdf-viewer ref="pdfComponent" v-if="receipt" :src="{data: receipt}"></pdf-viewer>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import Alert from '../blocks/Alert'
import HeaderRow from './HeaderRow'
import pdf from 'vue-pdf'

export default {
  name: 'Receipt',
  props: {
    service: { required: true }
  },
  data: () => ({
    hasError: false
  }),
  components: {
    appAlert: Alert,
    appHeaderRow: HeaderRow,
    pdfViewer: pdf
  },
  methods: {
    ...mapActions('personalCommon', ['getReceipt']),
    ...mapActions(['show_preloader']),
    ...mapActions('alert', ['showAlert'])
  },
  computed: {
    ...mapGetters('personalCommon', ['receipt', 'error'])
  },
  watch: {
    error (val) {
      this.showAlert({
        text: val
      })
    }
  },
  created () {
    this.show_preloader()
    this.getReceipt()
    this.hasError = Boolean(this.error)
  }
}
</script>

